@import "../../css-config/mixins.scss";

.main-header{
     width: 100%;
     padding: 0 20px;
     background-color: #212121;
     color: white;
     border-bottom: 1px solid rgba(0, 0, 0, 0.1);
     z-index: 99;
     @include md{
        padding: 0 40px;
     }
    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md {
            height: 80px;
        }
        .left{
            display: none;
            list-style-type: none;
            gap: 25px;
            @include md{
                display: flex;
            }
            li{
                font-size: 14px;
                font-weight: 600;
                text-transform: uppercase;
                cursor: pointer;
            }
        }
        .center{
            font-size: 22px;
            font-weight: 700;
            cursor: pointer;
            @include md {
                font-size: 34px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
        }
        .right{
            display: flex;
            align-items: center;
            gap: 20px;
            @include md {
                gap: 25px;

            }
            svg{
                cursor: pointer;
                font-size: 20px;
                @include md {
                    font-size: 24px;
                }
            }
            // .cart-icon{
            //     position: relative;
            //     span{
            //         min-width: 20px;
            //         text-align: center;
            //         background-color: #8e2de2;
            //         padding: 2.5px;
            //         position: absolute;
            //         top: -5px;
            //         right: -12px;
            //         font-size: 12px;
            //         line-height: 1;
            //         border-radius: 10px;
            //     }
            // }
        }
    }
    &.sticky-header{
        position: sticky;
        top: 0;
        transform: translateY(-80%);
        animation: stickyHeader 0.3s ease forwards;
    }
}

@keyframes stickyHeader {
    0% {transform: translateY(-80%);}
    100%{transform: translateY(0);}
}